import { TrinityEngine, THREE } from "../../engine/4.0";
import "./arimac.css";

let LoadingScreen = {};

let arimac = TrinityEngine.createScene(160, 160, false);
arimac.isLoading = true;
arimac.controls.enabled = false;

function render() {
    requestAnimationFrame(render);
    if(arimac.isLoading && arimac.logo) {
        arimac.composer.render();
        let r = arimac.logo.rotation;
        let y = r.y % (Math.PI * 2);
        let speed = 0.03;
        if(y > 0.3 && y < 5.8) { speed = 0.07; }
        arimac.logo.rotation.y += speed;
    }
}; render()

let light = new THREE.AmbientLight(0xffffff, 0.7);
arimac.scene.add(light);

LoadingScreen.start = function(color, callback = function() {}) {
    // elements and modules setup
    arimac.scene.background = new THREE.Color(color);
    arimac.root = document.querySelector('.arimac_load');
    arimac.root.style.background = color;
    arimac.renderer.domElement.style.background = color;

    let elem = document.createElement('div');
    elem.className = 'arimac_lval';
    arimac.root.appendChild(elem);
    if(color === '#FFF') {
        arimac.root.setAttribute('class', 'arimac_load arimac_load_white');
    }
    // load logo model
    TrinityEngine.loadModel('models/arimac/logo.fbx', function(logo) {
        arimac.root.appendChild(arimac.renderer.domElement);
        arimac.logo = logo;
        arimac.logo.rotation.y = 5.8;
        arimac.scene.add(logo);
        arimac.logo.scale.set(0.2, 0.2, 0.2);
        arimac.isLoading = true;
        if(color === '#FFF') {
            logo.children[0].material[1].color = new THREE.Color('#131313');
        }
        callback();
    });
    
};

LoadingScreen.setValue = function(value) {
    // element select
    let root = document.querySelector('.arimac_load');
    let elem = document.querySelector('.arimac_load>.arimac_lval');
    // update
    elem.innerHTML = 'Loading... ' + value + '%';
    // check end
    if(value === 100) {
        setTimeout(function() {
            root.style.opacity = 0;
            setTimeout(function() { root.style.display = 'none'; }, 300);
        }, 1000);
    }
};

LoadingScreen.end = function() {
    let root = document.querySelector('.arimac_load');
    setTimeout(function() {
        root.style.opacity = 0;
        setTimeout(function() { root.style.display = 'none'; }, 300);
    }, 1000); 
};

export default LoadingScreen;