import { Component } from 'react';
import { TrinityEngine, THREE } from './engine/4.0/';
import setup from './bin/setup';
import './bin/main.css';
import LoadingScreen from './bin/logo/arimac';

class App extends Component {

    componentDidMount() {
        LoadingScreen.start('#000', function() {
            q('#root').appendChild(modules.renderer.domElement);
            loadModel();
        });
    }

    render() {
        return (
            <div>
                <div className="arimac_load"></div>
                <div className="arimac_logo arimac_logo_white"></div>
            </div>
        );
    }

};

// ===================================================================

let w = window.innerWidth;
let h = window.innerHeight;
let q = function(x) { return document.querySelector(x); }

let modules = TrinityEngine.createScene(w, h);
let outline = TrinityEngine.createEffect(
    'Outline', modules.scene, modules.camera, modules.composer,
    { edgeStrength : 10, visibleEdgeColor : '#FFFFFF' }
);


window.addEventListener('resize', function() {
    w = window.innerWidth;
    h = window.innerHeight;
    TrinityEngine.setupScene(modules.composer, modules.camera, w, h);
});

// ===================================================================

var light = new THREE.PointLight(0xffffff, 0.8);
light.position.set(0, 2, 0);
modules.scene.add(light);

TrinityEngine.setupControls(
    'EXACT', modules.controls,
    { x : -1.5082423410982888, y : 1.0019399096423487, z : -1.0053198274219177 },
    { x : -1.5, y : 1, z : -1 },
    { maxPolarAngle : 1.8, minPolarAngle : 0.8 }
);

function loadModel() {
    var file = { room : 'models/eloiza_e_ze_newton/scene.gltf' };
    TrinityEngine.loadAll(
        file,
        function(obj) {
            modules.model = obj.room;
            modules.model.position.set(0, 0, 0);
            modules.model.scale.set(0.008, 0.008, 0.008);
            modules.scene.add(modules.model);
            addEvents();
        },
        function(n) {
            var p = parseInt(100 * n / 33);
            LoadingScreen.setValue(p);
        }
    );
}

modules.ground = new THREE.Mesh(
    new THREE.BoxGeometry(3.5, 0.1, 2.2),
    new THREE.MeshBasicMaterial({ transparent : true, opacity : 0 })
); modules.scene.add(modules.ground);

TrinityEngine.createEvent('click', modules.ground, function(e) {
    TrinityEngine.tweenControls('FIXED', modules.controls,      // type, controls
        { x : e.point.x, z : e.point.z },                       // new target pos
        e.distance * 350, 'power1',                             // time, ease
        function() { modules.controls.rotateSpeed = -0.3; }     // callback
    );
});

function addEvents() {
    Object.keys(setup).forEach(function(name) {
        var obj = setup[name];
        var mesh = TrinityEngine.findByName(modules.model, obj.mesh_name);
        // mouseenter + highlight object
        TrinityEngine.createEvent('mouseenter', mesh, function(e) {
            outline.selectedObjects = [mesh];
            modules.renderer.domElement.style.cursor = 'pointer';
        });
        // mouseleave + remove highlight object
        TrinityEngine.createEvent('mouseleave', mesh, function(e) {
            outline.selectedObjects = [];
            modules.renderer.domElement.style.cursor = 'default';
        });
        // click + focus object
        TrinityEngine.createEvent('click', mesh, function(e) {
            TrinityEngine.tweenControls(
                'EXACT', modules.controls,
                setup[name].focus_xyz.target,
                setup[name].focus_xyz.camera,
                1000
            );
        });
    });
};

TrinityEngine.tweenControls.captureSpot(modules.controls);

export default App;